
$primary:       #C14435;
$secondary:     #F4B334;
$tertiary:      #081E3F;
$light:         #FFFFFF;
$dark:          #484848;

@mixin transition($speed) {
  -webkit-transition: all $speed ease;
  -moz-transition: all $speed ease;
  -ms-transition: all $speed ease;
  -o-transition: all $speed ease;
  transition: all $speed ease;
}

@import 'clean-switch';
.seigal-small-staff-list {
  .staff-card {
    border-left: 2px dotted $primary;
    padding-left: 20px;
    font-size: .9em;
    span {
      display: inline-block;
      a {
        color: $primary;
      }
      &.name {
        font-weight: 900;
        display: block;
        line-height: 1.25em;
        font-size: 1.1em;
      }
      &.title {
        font-style: italic;
        display: block;
        line-height: 1.25em;

      }
      &.phone {}
      &.email {}
      &.icon {
        font-size: .8em;
        margin-right: 6px;
      }
    }
    .phone {
      white-space: nowrap;
    }
    .email {
      white-space: nowrap;
    }
  }
}
.seigal-stats-cards {
  .card-container {
    min-width: 33.33333%;
  }
  .stats-card {
    background: $light;
    -webkit-border-radius: 24px;
    border-radius: 24px;
    -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    @include transition(500ms);
    &:hover {
      background: #F7F7F7;
      @include transition(100ms);
    }
    span {
      display: block;
      text-align: center;
      &.number {
        font-size: 4em;
        font-weight: 900;
        color: $primary;
        line-height: 1em;
      }
      &.label {
        font-size: .85em;
        text-transform: uppercase;
        font-weight: 800;
        color: $primary;
      }
      &.description {
        font-size: .9em;
      }
    }
  }
}


.seigal-language-toggle {
    .toggle-content {
      .section:nth-child(1) {
        display: block;
        opacity: 1;
      }
      .section:nth-child(2) {
        display: none;
        opacity: 0;
      }


      &.toggle {
        .section:nth-child(1) {
          display: none;
          opacity: 0;
        }
        .section:nth-child(2) {
          display: block;
          opacity: 1;
        }
      }


    }
  }
.seigal-contact-info-block {
  .contact-container {
    a {
      color: $tertiary;
    }
    .contact-section {
      margin: 10px 0;
      &.social-media {
        font-size: 2em;
      }
      &.contact-section {
        .formatted_address {
          span {
            line-height: 1.2em;
            &.name, &.streetAddress {
              display: block;
            }
            &.addressCountry {
              display: none;
            }
          }
        }
      }
    }
    span.label {
      display: block;
      font-weight: 800;
      text-transform: uppercase;
      font-size: .85em;
    }
  }
}
.seigal-tribe-event-display {
  display: flex;
  flex-wrap: wrap;
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .event-container-large {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 40px;
    -webkit-border-radius: 24px;
    border-radius: 24px;
    -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    background: $light;
    padding-bottom: 0;
    padding-right: 0;
    .event-image {
      width: 50%;
      img {
        -webkit-border-radius: 24px 0 0 24px;
        border-radius:  24px 0 0 24px;
        height: 100%;
      }
    }
    .event-info {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .event-title-container {
        display: flex;
        flex-direction: row;
        padding: 20px;
        .date-container {
          text-align: center;
          background-color: $light;

          span {
            color: $primary;
            display: block;
            text-align: center;
            &.day {
              font-size: 58px;
              line-height: 50px;
              font-weight: 900;
            }
            &.month {
              text-transform: uppercase;
              font-weight: 400;
            }
          }
        }
        .event-title {
          h2 {
            margin-left: 20px;
            font-size: 28px;
            color: $tertiary;
          }
        }
      }
      .event-excerpt-container {
        background: $primary;
        padding: 20px;
        color: $light;
        font-size: 18px;
        line-height: 22px;
        -webkit-border-radius: 0 0 24px 0;
        border-radius: 0 0 24px 0;
      }
    }
  }

  .event-container-small {
    display: flex;
    -webkit-border-radius: 24px;
    border-radius: 24px;
    -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    background: $light;
    overflow: hidden;
    .date-container {
      text-align: center;
      background-color: $light;
      min-width: 100px;
      span {
        color: $primary;
        display: block;
        text-align: center;
        &.day {
          font-size: 58px;
          line-height: 50px;
          font-weight: 900;
        }
        &.month {
          text-transform: uppercase;
          font-weight: 400;
        }
      }
    }
    .event-info {
      color: $light;
      background: $primary;
      .event-title {
        h2 {
          color: $light;
          font-size: 24px;
          margin: 0px;
          margin-bottom: 8px;
        }
      }
      .event-excerpt-container {
        color: $light;
        font-size: 16px;
        line-height: 20px;
        -webkit-border-radius: 0 0 24px 0;
        border-radius: 0 0 24px 0;
      }
    }
  }

}

.seigal-page-header {
  .page-header-container {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    min-height: 150px;
    display: flex;
    justify-content: end;
    flex-direction: column;
    padding-right: 24px;
    &.has-image {
      min-height: 450px;
    }
    .title-block {
      background: $primary;
      padding-top: 6px;
      padding-left: 1.5em;
      padding-bottom: 10px;

      &.left {
        -webkit-border-radius: 0 24px 24px 0;
        border-radius: 0 24px 24px 0;
      }
      &.center {
        -webkit-border-radius: 24px 24px;
        border-radius: 24px 24px;
      }
      &.right {
        -webkit-border-radius: 24px 0 0 24px;
        border-radius: 24px 0 0 24px;
      }
      h1 {
        color: $light;
        margin-top: 15px;
        margin-bottom: 15px;
      }
      h2 {
        color: $light;
        font-weight: 400;
      }
    }
  }
}
body.home {
  .seigal-page-header {
    .page-header-container {
      min-height: 550px;
    }
  }
}

.seigal-edge-header {
  background: $primary;
  padding: 20px;
  color: $light;
  display: inline-block;
  margin-bottom: 15px;
  h1,h2,h3,h4,h5 {
    color: $light;
    margin: 0;
    padding: 0;
  }
  &.left {
    -webkit-border-radius: 0 24px 24px 0;
    border-radius: 0 24px 24px 0;
  }
  &.center {
    -webkit-border-radius: 24px 24px;
    border-radius: 24px 24px;
  }
  &.right {
    -webkit-border-radius: 24px 0 0 24px;
    border-radius: 24px 0 0 24px;
  }
}

.seigal-image-cta {
  .cta-image-container {
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 550px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    .title-block {
      -webkit-border-radius: 24px;
      border-radius: 24px;
      background: $tertiary;
      text-align: center;
      h1 {
        color: $light;
      }
      span {
        color: $light;
      }
      .title-block-footer {
        a {
          color: $light;
          padding: 10px 15px;
          border-bottom: 2px solid transparent;
          &:hover {
            text-decoration: none;
            border-bottom: 2px solid $light;
          }

          .arrow {
            margin-left: 15px;
            font-size: .8em;
          }
        }
      }
    }
  }
}


.seigal-contact-block {
  background: $secondary;
  color: $tertiary;
  margin-top: 80px;
  .form-container {
    margin-top: -60px;
    margin-bottom: -60px;
    -webkit-border-radius: 24px;
    border-radius: 24px;
    -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
    background: $light;
    min-height: 400px;
    padding: 20px;
  }
  .contact-container {
    span {
      &.label {
        display: block;
        font-weight: 800;
      }
    }
    .contact-section {
      margin-bottom: 20px;
      a {
        color: $tertiary;
      }
      span {
        &.name, &.streetAddress {
          display: block;
        }


      }
      span.addressCountry {
        display: none;
      }
    }
    .social-media {
      font-size: 2em;
    }
    .address {

    }
    .phone {

    }
    .email {

    }
  }
}

.seigal-program-card-block, .seigal-content-cards {
  .program-card {
    margin-bottom: 30px;
    .image {
      -webkit-border-radius: 24px 24px 0 0;
      border-radius: 24px 24px 0 0;
      overflow: hidden;
      z-index: -1;
      position: relative;
      img {
        width: 100%;
        height: auto;
      }
    }
    .program-card-body {
      background: $tertiary;
      -webkit-border-radius: 24px;
      border-radius: 24px;
      padding: 20px;
      margin-left: 24px;
      margin-right: 24px;
      margin-top: -60px;
      -webkit-box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
      box-shadow: 0 0 12px 0 rgba(0,0,0,.25);
      a {
        &:hover {
          text-decoration: none;
        }
      }
      h3 {
        font-size: 24px;
        color: $light;
        text-align: center;
      }
      .program-excerpt {
        color: $light;
        font-size: 16px;
        line-height: 1.5em;
        margin: 10px 0;
      }
      .program-card-footer {
        font-size: 18px;
        text-align: center;
        a {
          color: $light;
        }
      }
    }

  }
}

.seigal-donor-spotlight {
  width: 100%;
  background: #EFEFEF;
  margin-top: 125px;
  padding-bottom: 150px;
  margin-bottom: -150px;
  .donor-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .donor-image {
      margin-top: -125px;
      width: 250px;

      img {
        border: 5px solid darken(#EFEFEF, 20%);
      }
    }
    .donor-content {
      position: relative;
      h3 {
        margin-top: 0;
        text-align: center;
      }
      .label {
        text-align: center;
        width: 100%;
        text-transform: uppercase;
        font-size: .8em;
        font-weight: 900;
        margin-top: 30px;
        color: $dark;
      }
      hr {
        width: 25%;
        margin: 15px auto;
      }
      .spotlight-container {
        position: relative;
        padding: 0 50px;
        span {
          &.description {
            font-size: .9em;
            text-align: justify;
            position: relative;

          }
          &.open-quote, &.close-quote{
            padding: 10px;
            font-size: 2.5em;
            position: absolute;
            top: 120px;
          }
          &.open-quote {
            left: -20px;
          }
          &.close-quote {
            right: -20px;
          }
        }
      }

    }
  }
}

.seigal-edge-aligned-image {
  width: 100%;
  height: 100%;
  .image-container {
    height: 100%;
    min-height: 450px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    &.left {
      -webkit-border-radius: 0 24px 24px 0;
      border-radius: 0 24px 24px 0;
    }
    &.center {
      -webkit-border-radius: 24px 24px;
      border-radius: 24px 24px;
    }
    &.right {
      -webkit-border-radius: 24px 0 0 24px;
      border-radius: 24px 0 0 24px;
    }
  }
}


@media (max-width: 576px) {
  .seigal-page-header {
    .page-header-container {
      padding-right: 0;
      .title-block {
        margin: 0 24px;
        h1 {
          font-size: 26px;
        }
        &.left {
          -webkit-border-radius: 24px;
          border-radius: 24px;
        }
        &.center {
          -webkit-border-radius: 24px;
          border-radius: 24px;
        }
        &.right {
          -webkit-border-radius: 24px;
          border-radius: 24px;
        }
      }
    }
  }

  .seigal-image-cta {
    .cta-image-container {
      padding: 8px;
    }
  }


  .seigal-tribe-event-display {
    .event-container-large {
      flex-direction: column;
      margin-bottom: 40px;
      .event-image {
        width: 100%;
        img {
          -webkit-border-radius: 24px 24px 0 0;
          border-radius: 24px 24px 0 0;
          height: 100%;
        }
      }
      .event-info {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .event-title-container {
          padding: 20px;
          .date-container {
            span {
              &.day {
                font-size: 58px;
                line-height: 50px;
              }
              &.month {

                font-weight: 400;
              }
            }
          }
          .event-title {
            h2 {
              margin-left: 20px;
              color: $tertiary;
            }
          }
        }
        .event-excerpt-container {
          font-size: 18px;
          line-height: 22px;
          -webkit-border-radius: 0 0 24px 24px;
          border-radius: 0 0 24px 24px;
        }
      }
    }

    .event-container-small {
      margin-bottom: 24px;
      .date-container {

        span {
          color: $primary;
          display: block;
          text-align: center;
          &.day {
            font-size: 58px;
            line-height: 50px;
            font-weight: 900;
          }
          &.month {
            text-transform: uppercase;
            font-weight: 400;
          }
        }
      }
      .event-info {
        color: $light;
        background: $primary;
        .event-excerpt-container {
          display: none;
        }
      }
    }

  }


  .seigal-contact-block {

    .form-container {
      margin-bottom: 60px;
    }

  }

  .seigal-donor-spotlight {
    .donor-info {
      .donor-content {
        span {
          &:before, &:after {
            display: none;
          }
        }
      }
    }
  }


}

// Small styles - default: 480px <= x <= 991px
@media (min-width: 576px) and (max-width: 992px) {
  .seigal-donor-spotlight {
    .donor-info {
      .donor-content {
        span {
          &:before, &:after {
            display: inline-block;
          }
        }
      }
    }
  }
}

// Medium styles - default: 992px <= x <= 1199px
@media (min-width: 992px) and (max-width: 1200px) {
  .seigal-donor-spotlight {
    .donor-info {
      .donor-content {
        span {
          &:before, &:after {
            display: inline-block;
          }
        }
      }
    }
  }
}

// Large styles - default: 1200px <= x
@media (min-width: 1200px) {

  .seigal-donor-spotlight {
    .donor-info {
      .donor-content {
        span {
          &:before, &:after {
            display: inline-block;
          }
        }
      }
    }
  }



}
